.contacts__title, .price__title, .portfolio__title, .gallery__title {
  font-family: "Caveat";
  font-size: 24px;
  color: maroon;
  text-align: center;
}

.footer__social-link, .footer__contacts-link, .header__info-btn, .nav-link {
  position: relative;
}
.footer__social-link:hover, .footer__contacts-link:hover, .header__info-btn:hover, .nav-link:hover {
  color: inherit;
  border-radius: 5px;
  background-color: rgba(128, 0, 128, 0.1);
  backdrop-filter: blur(3px);
  -webkit-backdrop-filter: blur(3px);
}
.footer__social-link:hover::after, .footer__contacts-link:hover::after, .header__info-btn:hover::after, .nav-link:hover::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  background-color: maroon;
  height: 2px;
  width: 100%;
}

.contacts .kolco-box, .price .kolco-box, .portfolio .kolco-box, .gallery .kolco-box, .about .kolco-box {
  position: relative;
  z-index: 3;
}
.contacts .kolco, .price .kolco, .portfolio .kolco, .gallery .kolco, .about .kolco {
  position: absolute;
  top: -42px;
  height: 60px;
  width: 100%;
  background-image: url(../img/pink_new.svg);
  background-repeat: repeat;
}

@font-face {
  font-family: "Caveat";
  src: url("../fonts/Caveat-Medium.ttf") format("truetype");
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.ttf") format("truetype");
}
html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

ul[class],
ol[class] {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
  font-size: inherit;
}

ul[class] {
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

a {
  text-decoration: none;
}

html,
body {
  height: 100%;
  font-weight: 400;
  font-size: 16px;
  line-height: 28px;
  color: maroon;
  font-family: "Roboto";
  overflow-x: hidden;
  min-width: 300px;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.main {
  flex: 1 0 auto;
  color: #8B008B;
}

.footer {
  flex: 0 0 auto;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

.container-fluid {
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 10px;
}

.header {
  -webkit-box-shadow: 0px 0px 5px 3px rgb(177, 126, 177);
  -moz-box-shadow: 0px 0px 5px 3px rgb(177, 126, 177);
  box-shadow: 0px 0px 5px 3px rgb(177, 126, 177);
  background-color: #fed0d2;
}
.header__inner {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
  height: 620px;
}

.logo_img {
  position: relative;
  display: block;
  height: 620px;
  object-fit: cover;
  position: absolute;
  top: 0;
  right: 0px;
  z-index: 1;
}

.navbar {
  position: relative;
  z-index: 2;
  width: 100%;
}

.nav {
  display: flex;
  margin-top: 20px;
}
.nav-item {
  padding: 0px 20px;
}
.nav-link {
  font-family: "Caveat";
  color: maroon;
  font-size: 22px;
}

.header__info {
  display: flex;
  width: 100%;
  flex-direction: column;
  position: absolute;
  bottom: 40px;
  z-index: 2;
  color: maroon;
  font-family: "Roboto";
  padding: 10px 15px 10px 35px;
  border-radius: 5px;
  background-color: rgba(128, 0, 128, 0.1);
  backdrop-filter: blur(3px);
  -webkit-backdrop-filter: blur(3px);
}
.header__info-title {
  margin-left: 35px;
  font-size: 18px;
  text-transform: uppercase;
}
.header__info-text {
  margin: 10px 0;
  font-size: 56px;
  font-family: "Caveat";
  color: inherit;
}
.header__info-btn {
  margin: 5px;
  background-color: transparent;
  max-width: 220px;
}
.header__info-btn a {
  color: maroon;
}

.footer {
  background-color: #fed0d2;
}
.footer__inner {
  height: 120px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.footer__social-list {
  margin: 30px 0;
  display: inline-block;
}
.footer__social-link, .footer__contacts-link {
  padding: 0 10px;
  font-family: "Caveat";
  font-size: 22px;
  color: maroon;
  padding-left: 28px;
  position: relative;
}
.footer__social-link::before, .footer__contacts-link::before {
  content: "";
  position: absolute;
  left: 0;
  top: 8px;
  width: 16px;
  height: 16px;
}
.footer__social-link--facebook::before {
  background-image: url(../img/facebook-square.svg);
  width: 17px;
  height: 20px;
}
.footer__social-link--instagram::before {
  background-image: url(../img/instagram.svg);
  width: 18px;
  height: 20px;
}
.footer__contacts-link--mail::before {
  background-image: url(../img/envelope.svg);
  width: 20px;
  height: 20px;
}

.about .container,
.contacts .container,
.portfolio .container {
  background-color: #FFEFD5;
  -webkit-box-shadow: 0px 0px 5px 3px rgb(179, 153, 111);
  -moz-box-shadow: 0px 0px 5px 3px rgb(179, 153, 111);
  box-shadow: 0px 0px 5px 3px rgb(179, 153, 111);
  margin-top: 24px;
}

.about__inner-title {
  text-align: center;
  color: maroon;
  margin-top: 60px;
  font-size: 24px;
  font-family: "Caveat";
}
.about__inner-text {
  font-size: 16px;
}
.about__img img {
  min-height: 400px;
  object-fit: cover;
  margin: 50px auto;
  padding: 10px;
}

.gallery .container,
.price > .container {
  background-color: #fed0d2;
  -webkit-box-shadow: 0px 0px 5px 3px rgb(177, 126, 177);
  -moz-box-shadow: 0px 0px 5px 3px rgb(177, 126, 177);
  box-shadow: 0px 0px 5px 3px rgb(177, 126, 177);
  margin-top: 24px;
}

.gallery__title {
  margin-top: 40px;
}

.video-container {
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 1fr 1.3fr;
  gap: 10px;
  align-items: flex-start;
  padding: 5px 5% 50px;
}
.video-container .video-list {
  display: flex;
  flex-wrap: wrap;
  background: #FFEFD5;
  border-radius: 5px;
  overflow-y: scroll;
  max-height: 500px;
}
.video-container .video-list .vid {
  align-items: center;
  background: #f7f7f7;
  border-radius: 5px;
  margin: 10px 0 0 10px;
  padding: 10px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  cursor: pointer;
  height: 230px;
}
.video-container .video-list .vid:hover {
  background: #eee;
}
.video-container .video-list .vid.active {
  background: #2980b4;
}
.video-container .video-list .vid.active .video-title {
  color: #fff;
}
.video-container .video-list .vid video {
  width: 100px;
  border-radius: 5px;
}
.video-container .video-list::-webkit-scrollbar {
  width: 7px;
}
.video-container .video-list::-webkit-scrollbar-track {
  background: #ccc;
  border-radius: 50px;
}
.video-container .video-list::-webkit-scrollbar-thumb {
  background: #666;
  border-radius: 50px;
}

.video-container .main-video {
  background: #FFEFD5;
  border-radius: 5px;
  padding: 10px;
}
.video-container .main-video video {
  max-height: 600px;
  width: 100%;
  border-radius: 5px;
}
.video-container .main-video .video-title {
  text-align: center;
  color: #333;
  font-size: 23px;
  padding-top: 10px;
  padding-bottom: 10px;
}

.video-container .main-video .video {
  max-height: 650px;
}

.portfolio__title {
  margin-top: 40px;
}
.portfolio__cards-item {
  margin-bottom: 40px;
}
.portfolio__gallery {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  justify-content: center;
  padding-bottom: 50px;
}

.gallery-item__thumb {
  -webkit-box-shadow: 0px 0px 6px 2px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 6px 2px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 6px 2px rgba(0, 0, 0, 0.75);
  object-fit: cover;
  border-radius: 20px;
  width: auto;
  height: 300;
}
.gallery-item__thumb:hover {
  transform: scale(1.05);
}

.price__title {
  margin-top: 40px;
}
.price .table {
  margin-bottom: 80px;
}

.contacts__title {
  margin-top: 40px;
}
.contacts_item {
  margin: 25px;
}
.contacts_item a {
  text-decoration: none;
  color: maroon;
  padding: 10px;
  border-radius: 10px 0 10px 0;
}

@media (max-width: 992px) {
  .nav-link {
    position: relative;
  }
  .nav-link:hover::after {
    width: 90px;
  }
  .navbar {
    width: max-content;
  }
  .video-container {
    grid-template-columns: 1fr 1fr;
  }
  .portfolio__gallery {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (max-width: 840px) {
  .video-container {
    grid-template-columns: 1.5fr 1fr;
  }
}
@media (max-width: 768px) {
  .video-container {
    grid-template-columns: 1fr;
    padding: 10px 10px 50px 10px;
  }
  .video-container .video-list {
    flex-wrap: nowrap;
    height: 260px;
    overflow-x: scroll;
    overflow-y: hidden;
  }
  .video-container .video-list .vid video {
    width: 100px;
  }
  .video-container .video-list::-webkit-scrollbar {
    height: 7px;
  }
}
@media (max-width: 650px) {
  .header__info-title {
    color: #fff;
  }
  .header__info-text .ira_name {
    color: #fff;
  }
  .header__info-btn {
    background-color: rgba(128, 0, 128, 0.4);
  }
  .header__info-btn a {
    color: #fff;
  }
}
@media (max-width: 576px) {
  .about__inner-title {
    margin-top: 0;
  }
  .about__inner-text {
    font-size: 16px;
    margin-bottom: 50px;
  }
  .about__img img {
    min-height: 400px;
    object-fit: cover;
    margin: 50px auto 10px;
    padding: 10px;
  }
}
@media (max-width: 540px) {
  .navbar-nav {
    max-width: 100px;
    border-radius: 5px;
    background-color: rgba(128, 0, 128, 0.1);
    backdrop-filter: blur(3px);
    -webkit-backdrop-filter: blur(3px);
  }
  .nav {
    display: flex;
  }
  .nav-item {
    padding: 0 5px;
  }
}